import { Injectable } from '@angular/core';
import { IManagedObject, InventoryService, QueriesUtil } from '@c8y/client';
import { NavigatorNode, NavigatorService, gettext } from '@c8y/ngx-components';
import { ReportDashboardService } from './report-dashboard.service';

@Injectable()
export class ReportDashboardNavigationFactory {
  loaded: boolean = false;

  constructor(
    private navigatorService: NavigatorService,
    private inventoryService: InventoryService,
    private reportDashboardService: ReportDashboardService
  ) {}

  async get() {
    if (!this.loaded) {
      this.loaded = true;

      this.navigatorService.add(
        new NavigatorNode({
          label: gettext('Reports'),
          path: 'reports',
          icon: 'c8y-reports',
          priority: 5
        })
      );

      const reports: IManagedObject[] = (await this.reportDashboardService.listReports({
        filter: { __has: 'c8y_IsNavigatorNode' },
        pageSize: 2000
      })).data;
      reports.forEach(report => {
        const node = new NavigatorNode({
          label: report.name,
          path: `reports/${report.id}`,
          icon: report.icon,
          priority: report.priority,
          routerLinkExact: false
        });
        this.navigatorService.add(node);
      });
    }
  }
}
