import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { CoreModule, gettext, HOOK_NAVIGATOR_NODES, HOOK_ONCE_ROUTE } from '@c8y/ngx-components';
import { ContextDashboardModule } from './context-dashboard.module';
import { DeviceManagementHomeDashboardComponent } from './deviceManagement-home-dashboard/deviceManagement-home-dashboard.component';

@NgModule({
  declarations: [DeviceManagementHomeDashboardComponent],
  imports: [CommonModule, CoreModule, ContextDashboardModule],
  exports: [],
  entryComponents: [DeviceManagementHomeDashboardComponent],
  providers: [
    {
      provide: HOOK_ONCE_ROUTE,
      useValue: {
        path: '',
        component: DeviceManagementHomeDashboardComponent
      },
      multi: true
    },
    {
      provide: HOOK_NAVIGATOR_NODES,
      useValue: [{ label: gettext('Home'), icon: 'home', priority: 10000, path: '/' }],
      multi: true
    }
  ]
})
export class DeviceManagementHomeDashboardModule {}
