import { Component, OnInit } from '@angular/core';
import { gettext, Widget } from '@c8y/ngx-components';
import { WidgetService } from '../widget.service';

@Component({
  selector: 'c8y-device-management-home-dashboard',
  templateUrl: './deviceManagement-home-dashboard.component.html'
})
export class DeviceManagementHomeDashboardComponent implements OnInit {
  widgets = [];
  DEFAULT_HOME_WIDGETS = [
    {
      name: 'Asset Alarms',
      title: gettext('Active, critical alarms'),
      _x: 0,
      _y: 0,
      _height: 4,
      _width: 6
    },
    {
      name: 'Map',
      title: gettext('Map'),
      _x: 6,
      _y: 0,
      _height: 8,
      _width: 6
    }
  ] as Widget[];

  constructor(private widgetService: WidgetService) {}

  ngOnInit() {
    this.DEFAULT_HOME_WIDGETS.map(async widget => {
      await this.widgetService
        .mapLegacy(widget)
        .then(transformedWidget => this.widgets.push(transformedWidget));
    });
  }
}
